import { Link, useNavigate, useParams } from "react-router-dom";
import "./UserNav.css";
import { useContext } from "react";
import AppContext from "../AppContext";
import { removeAuthHeader } from "../apis/axiosConfig";
import Swal from "sweetalert2";

const UserNav = ({ reservationId }) => {
  const appContext = useContext(AppContext);

  const navigate = useNavigate();

  const handleLogout = (event) => {
    removeAuthHeader();
    //context  
    appContext.setUser("");
    appContext.setAccessToken("");
    appContext.setRole("");
    Swal.fire({
      icon: "success",
      title: "Logout",
      text: " .",
      showCancelButton: false,
      confirmButtonText: "", 
      confirmButtonColor: "#FFCD4A",
      customClass: {
        confirmButton: "no-outline",
      }
    });
    navigate("/");
  };

  return (
    <usernav>
      <div className="user_nav">
        <div className="header_img">
          <Link to="/user">
            <img src="/assets/text_logo_black.png" />
          </Link>
        </div>
        <div className="user_nav_item">
          <Link to="/user/search">
            <h3> </h3>
          </Link>
          <div className="user_dropdown_content">
            <div className="user_dropdown_content_left">
              <Link to="/user/search">
                <p>   </p>
              </Link>
              <Link to={`/user/reservationstatus/${reservationId}`}>
                <p>  </p>
              </Link>
            </div>
          </div>
        </div>
        <div className="user_nav_item">
          <Link to="/user/myinfo">
            <h3> </h3>
          </Link>
          <div className="user_dropdown_content">
            <div className="user_dropdown_content_right">
              <Link to="/user/myinfo">
                <p>  </p>
              </Link>
              <Link to="/user/myreservation">
                <p>  </p>
              </Link>
            </div>
          </div>
        </div>
        <div className="header_right">
          {appContext.user === "" ? (
            <>
              <Link to="/login">
                <p></p>
              </Link>
              <p>|</p>
              <Link to="/register">
                <p></p>
              </Link>
            </>
          ) : (
            <div className="loginafterbox">
              <span className="userNameDiv">
                {appContext.user} !
              </span>
              <button className="logoutButton" onClick={handleLogout}>
                
              </button>
            </div>
          )}
        </div>
      </div>
    </usernav>
  );
};
export default UserNav;
